import React from 'react';
import kaspi from '../../../../assets/images/kaspi.jpg';
import cls from '../MyProfile/MyProfile.module.css';

const MyWallet = () => {
  return (
    <div className = {cls.my}>
      <div className = {cls.header}>
        <div className = {cls.title}>   </div>
        <div className = {cls.money}> : 5000 KZT </div>
      </div>
      <hr className={cls.hr} />

      <div className = {cls.content_wallet}>
        <div className = {cls.sub__title}>    </div>

        <div className = {cls.description__block}>
          <img src = { kaspi } alt = '' />
          <div className = {cls.description__steps}>
            <div className = {cls.step}> 1.  ,          .  </div>
            <div className = {cls.step}> 2.  ,          .  </div>
            <div className = {cls.step}> 3.  ,          .  </div>
          </div>
        </div>

      </div>

    </div>
  )
}

export default MyWallet;